#button {
  width: 50px;
  height: 50px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  background-color: #ffa5ba;
  border-radius: 50%;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

#button:after {
  content: "Up";
  color: #fff;
  font-family: Titan One, sans-serif;
  font-size: 2em;
  font-style: normal;
  font-weight: normal;
  line-height: 50px;
}

#button:hover, #button:focus {
  cursor: pointer;
  background-color: #e17992;
}

#button:active {
  background-color: #d31342;
}

#button.show {
  opacity: 1;
  visibility: visible;
}

